    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}


.home-mainPicture {
    height: 90%;
}

.home-addToChrome {
    margin-top: 20px;
}


.home-rotatingTextSection {
    font: normal 40px/50px Roboto, sans-serif;
    font-weight: 600;
    color: #999;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.home-rotatingH1 {
    height: 50px;
    margin-right: 0.3em;
    font-size: 45px;
    font-weight: 600;
}

.home-rotatingTextSection .home-b {
    overflow: hidden;
    height: 50px;
}

.home-rotatingTextSection .innerIam {
    font: normal 40px/50px Roboto, sans-serif;
    font-weight: 600;
    display: inline-block;
    color: var(--primary2);
    position: relative;
    white-space: nowrap;
    top: 0;
    left: 0;


    /*animation*/
    -webkit-animation: move 6s;
    -moz-animation: move 6s;
    -ms-animation: move 6s;
    -o-animation: move 6s;
    animation: move 6s;
    /*animation-iteration-count*/
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    /*animation-delay*/
    -webkit-animation-delay: 1s;
    -moz-animation-delay: 1s;
    -ms-animation-delay: 1s;
    -o-animation-delay: 1s;
    animation-delay: 1s;
}

@keyframes move {
    0% {
        top: 0px;
    }

    20% {
        top: -50px;
    }

    40% {
        top: -100px;
    }

    60% {
        top: -150px;
    }

    80% {
        top: -200px;
    }
}


.home-mainTextSection p {
    width: 70%;
    margin-top: 10px;
    color: var(--text2);
    font-weight: 400;
    font-size: 18px;
}



.magicalSpan {
    background-image: linear-gradient(-225deg,
            #231557 0%,
            #44107a 19%,
            #ff1361 57%,
            #fff800 100%);
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 5s ease-in-out infinite;
    display: inline-block;
    font-size: 40px;
    font-weight: 600;
}

@keyframes textclip {
    to {
        background-position: 200% center;
    }
}




